.nusa {
  margin-top: -150px;
  padding-bottom: 250px;
  position: relative;
  z-index: 1;
}

.nusa__body {
  display: flex;
  justify-content: space-between;
}

.nusa__leftColumn {
  flex-shrink: 0;
  height: 720px;
  position: relative;
  width: 598px;
}

.nusa__rightColumn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 705px;
  padding: 40px;
  width: 100%;
}

.nusa__secondTitle {
  margin-bottom: 27px;

  &::before {
    margin-right: 24px;
  }

  &::after {
    display: none;
  }
}

.nusa__firstTitle {
  margin-bottom: 27px;
  max-width: 555px;
}

.nusa__text {
  margin-bottom: 27px;
}

