.reedMore {
  align-items: center;
  align-self: flex-start;
  color: #fbd784;
  display: flex;
  font-weight: 600;
  font-size: 1.8rem;
  line-height: 1.39;
  transition: opacity 0.3s;

  svg {
    fill: #fbd784;
    margin-left: 9px;
  }

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}