.linkHover {
  display: inline-block;
  position: relative;

  &:hover,
  &:focus {

    &::before {
      opacity: 1;
    }
  }

  &::before {
    background-color: #ffffff;
    bottom: -4px;
    content: '';
    display: block;
    height: 1px;
    left: 0;
    opacity: 0;
    position: absolute;
    transition: opacity 0.3s;
    right: 0;
  }
}