.bromo {
  padding-bottom: 227px;
}

.bromo__body {

}

.bromo__slider {
  width: 100%;

  .slick-arrow {
    z-index: 1;
  }

  .slick-prev {
    left: 50px;

    &::before {
      content: url(/assets/src/img/common/slider_arrowL.svg);
    }
  }

  .slick-next {
    right: 50px;

    &::before {
      content: url(/assets/src/img/common/slider_arrowR.svg);
    }
  }
}

.bromo__sliderItem {

}

.bromo__sliderItemBody {
  align-items: flex-end;
  display: flex;
  justify-content: center;
  height: 985px;
  padding: 40px;
  position: relative;

  img {
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: absolute;
    z-index: 0;
  }
}

.bromo__sliderItemContent {
  align-items: center;
  display: flex;
  flex-direction: column;
  position: relative;
  text-align: center;
  z-index: 1;
}

.bromo__secondTitle {
  margin-bottom: 24px;

  &::before {
    margin-right: 29px;
  }

  &::after {
    margin-left: 29px;
  }
}

.bromo__firstTitle {
  margin-bottom: 27px;
  max-width: 730px;
}

.bromo__text {
  margin-bottom: 27px;
  max-width: 934px;
}

.bromo__reedMore {
  align-self: center;
}