@import "/assets/src/scss/styles/fonts.scss";

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;  //10px
}

body {
  font-family: OpenSans, Arial, sans-serif;
  color: #ffffff;
}

a:not(.outline),
input:not(.outline),
textarea:not(.outline),
button:not(.outline) {
  outline: none;
}

a:not(.text-decoration) {
  text-decoration: none;
}

ul:not(.list-style) {
  list-style: none;
}

button:not(.cursor) {
  cursor: pointer;
}

img {
  object-fit: cover;
}

.wrapper {
  background-color: #000000;
  display: flex;
  flex-direction: column;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1;
  min-height: 100vh;
}

.container {
  margin: 0 auto;
  max-width: 1500px;
  padding: 0 20px;
  width: 100%;
}

.container--outer {
  margin: 0 auto;
  max-width: 1920px;
  padding: 0 85px;
  width: 100%;
}

.main {
  flex-grow: 1;
}

.blockHide {
  display: none;
}

.blockTitle--1 {
  font-weight: 700;
  line-height: 1.04;
  text-transform: capitalize;
}

.blockTitle--2 {
  align-items: center;
  color: #ffc178;
  display: flex;
  font-weight: 700;
  line-height: 1.4;
  letter-spacing: 6px;
  text-transform: uppercase;

  &::before,
  &::after {
    content: "";
    display: block;
    background-color: #ffc178;
    height: 2px;
    position: relative;
    width: 72px;
  }
}

.blockTitle--3 {
  font-weight: 700;
  line-height: 1.36;
}

.blockText {
  font-weight: 600;
  line-height: 1.78;
}

.blockBtn {
  align-items: center;
  background-color: #ffa343;
  border: none;
  display: flex;
  border-radius: 8px;
  font-weight: 700;
  line-height: 1.29;
  justify-content: center;
  text-align: center;
  transition: opacity 0.3s;

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}

.fz15 {
  font-size: 1.5rem;
}

.fz17 {
  font-size: 1.7rem;
}

.fz18 {
  font-size: 1.8rem;
}

.fz19 {
  font-size: 1.9rem;
}

.fz20 {
  font-size: 2rem;
}

.fz22 {
  font-size: 2.2rem;
}

.fz36 {
  font-size: 3.6rem;
}

.fz64 {
