.footer {

}

.footer__body {

}

.footer__block--1 {
  align-items: center;
  border-bottom: 1px solid rgba(255,255,255,0.3);
  display: flex;
  margin-bottom: 55px;
  padding-bottom: 55px;

  span {
    flex-grow: 1;
    margin-right: 55px;
    text-align: right;
  }

  .blockBtn {
    color: #000000;
    justify-self: flex-end;
    padding: 24px 70px;
  }
}

.footer__logo {
  height: 65px;
  width: 173px;
}

.footer__block--2 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 135px;

  form {
    border-bottom: 1px solid rgba(255,255,255,0.3);
    display: flex;

    input {
      color: #ffffff;
      background-color: transparent;
      border: none;
      flex-grow: 1;
      @include placeholder(14px);
    }

    button {
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
      padding: 29px;

      svg {
        fill: #000000;
      }
    }
  }
}

.footer__mail {
  max-width: 400px;
}

.footer__mailTitle {
  margin-bottom: 50px;
  font-weight: 700;
  line-height: 1.36;
}

.footer__menu {
  display: flex;
  justify-content: space-between;
  margin-right: 96px;
  width: 49%;
}

.footer__menuItem {

  ul {
    margin: -22px -10px;
  }

  li {
    margin: 22px 10px;
  }

  a {
    color: #ffffff;
  }
}

.footer__menuTitle {
  color: #FFD1A0;
  font-weight: 700;
  margin-bottom: 45px;
}

.footer__block--3 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 156px;
}

.footer__doc {

}

.footer__terms,
.footer__policy {
  color: #ffffff;
  margin: 0 40px;
}

.footer__socials {
  transform: rotate(180deg);

  li {
    margin-right: 50px;
    transform: rotate(180deg);

    &:last-child {
      margin-right: 0;
    }
  }

  a {
    height: 28px;
    width: 28px;
  }
}

.footer__block--4 {
  margin-bottom: 118px;
}

.footer__copyright {
  color: rgba(255,255,255,0.5);
  text-align: center;
}



