.header {
  left: 0;
  padding-top: 64px;
  top: 0;
  position: absolute;
  z-index: 2;
  width: 100%;

  .container--outer {
    align-items: center;
    display: flex;
    justify-content: space-between;
  }

  .container {
    flex-grow: 1;
  }
}

.header__logo {
  height: 32px;
  width: 96px;
}

.header__body {

}

.header__menu {
  margin: 0 -25px;

  ul {
    align-items: center;
    display: flex;
    justify-content: center;
  }

  li {
    margin: 0 25px;
  }

  a {
    color: #ffffff;
  }
}

.header__search {
  align-items: center;
  display: flex;
  flex-shrink: 0;
  overflow: hidden;

  button {
    align-items: center;
    background-color: transparent;
    border: none;
    display: flex;
    justify-content: center;
    height: 20px;
    margin-right: 5px;
    order: 1;
    transform: translateX(160px);
    transition: transform 0.3s;
    width: 20px;

    svg {
      fill: rgba(255,255,255,0.7);
    }
  }

  input {
    border: 1px solid transparent;
    border-radius: 5px;
    background-color: transparent;
    color: #ffffff;
    font-family: OpenSans,sans-serif;
    order: 2;
    padding: 5px 10px;
    transform: translateX(160px);
    transition: transform 0.3s, border-color 0.3s;
    @include placeholder(17px);

    &:focus {
      border-color: #ffffff;
      transform: translateX(0);

      &+button {
        transform: translateX(0);
      }
    }
  }
}