.indonesian {
  padding-bottom: 250px;
  position: relative;
  z-index: 1;
}

.indonesian__body {
  display: flex;
  justify-content: space-between;
}

.indonesian__leftColumn {
  flex-shrink: 0;
  height: 720px;
  order: 2;
  position: relative;
  width: 598px;
}

.indonesian__rightColumn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 705px;
  order: 1;
  padding: 40px;
  width: 100%;
}

.indonesian__secondTitle {
  margin-bottom: 27px;

  &::before {
    margin-right: 24px;
  }

  &::after {
    display: none;
  }
}

.indonesian__firstTitle {
  margin-bottom: 27px;
  max-width: 555px;
}

.indonesian__text {
  margin-bottom: 27px;
}

