.welcome {
  padding: 285px 0 875px 0;
  position: relative;

  .container--outer {
    display: flex;
    position: relative;
    z-index: 2;
  }
}

.welcome__bgShadow--1 {
  background: linear-gradient(330.24deg, rgba(0, 0, 0, 0) 31.06%, #000000 108.93%);
  bottom: 0;
  left: 0;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.welcome__bgShadow--2 {
  background: linear-gradient(360deg, #000000 0%, rgba(0, 0, 0, 0) 77.78%);
  bottom: 0;
  height: 358px;
  left: 0;
  position: absolute;
  right: 0;
  z-index: 1;
}

.welcome__body {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.welcome__secondTitle {
  margin-bottom: 32px;

  &.blockTitle--2 {

    &::before {
      margin-right: 32px;
    }

    &::after {
      display: none;
    }
  }
}

.welcome__mainTitle {
  margin-bottom: 125px;

  div {
    &:last-child {
      font-weight: 400;
    }
  }
}

.welcome__scrollBtn {
  align-items: center;
  color: #ffffff;
  display: flex;

  svg {
    fill: #ffffff;
    margin-left: 9px;
  }
}

.welcome__socialWrapper {
  padding-top: 70px;
  width: 25px;
}

.welcome__social {
  align-items: center;
  display: flex;
  position: sticky;
  transform: rotate(90deg);
  top: 20px;

  .socials {

    li {
      margin-right: 20px;
      transform: rotate(-90deg);

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.welcome__socialText {
  flex-shrink: 0;
  margin-right: 20px;
}